<script setup>
import { ref, defineEmits } from 'vue';

// Ê∑ªÂä†ÊóãËΩ¨ÊïàÊûúÁöÑÁä∂ÊÄÅ
const isActive = ref(false);
const emit = defineEmits(['print-tags']);

const emitPrintTags = () => {
