    emit('print-tags');

    // Ê∑ªÂä†‰∏Ä‰∏™ÂÆöÊó∂Âô®ÔºåÂú®ÊóãËΩ¨Âä®ÁîªÁªìÊùüÂêéÁßªÈô§ active Á±ª
    setTimeout(() => {
        isActive.value = false; // ÁßªÈô§ active Á±ª
    }, 500); // 500ms ‰∏é CSS ‰∏≠ÁöÑÊóãËΩ¨Êó∂Èó¥‰∏ÄËá¥
};
</script>

<style scoped>
.align-button {
    display: flex;
    align-items: center;
    justify-content: center;
    width: 100%;
}

.refresh-icon {
