<template>
    <div class="home-page">
        <TagSelector @tag-change="handleTagSelectorChange" />
        <ContentTags @print-tags="printSelectedTags" />
        <!-- ‰º†ÈÄí videos Âíå isLoading Êï∞ÊçÆÁªô VideoGallery ÁªÑ‰ª∂ -->
        <VideoGallery :videos="videos" :isLoading="isLoading" />
    </div>
</template>

<script>
import TagSelector from './TagSelector.vue';
import ContentTags from './ContentTags.vue';
import VideoGallery from './VideoGallery.vue';
import axios from 'axios';

export default {
    name: "HomePage",
    components: {
        TagSelector,
        ContentTags,
        VideoGallery
    },
    data() {
        return {
            tagSelectorTags: [],
            contentTagsTags: [],
            videos: [], // Â≠òÂÇ®‰ªéÂêéÁ´ØËé∑ÂèñÁöÑËßÜÈ¢ëÊï∞ÊçÆ
            isLoading: false,
        };
    },
    methods: {
        handleTagSelectorChange(tags) {
            this.tagSelectorTags = tags;
        },
        async printSelectedTags() {
            this.videos = [];
            this.isLoading = true;
            try {
                // ÂèëÈÄÅ POST ËØ∑Ê±Ç
                const response = await axios.post('https://www.hypervapor.cloud/api/scrape', {
                    tags: this.tagSelectorTags
                });

                // ÈÅçÂéÜÂìçÂ∫î‰∏≠ÁöÑÊØè‰∏™ÂØπË±°ÁöÑ result Êï∞ÁªÑÔºåÂπ∂Â∞ÜÂÖ∂ÊâÅÂπ≥ÂåñÂä†ÂÖ•Âà∞ this.videos ‰∏≠
                response.data.forEach(item => {
                    if (Array.isArray(item.result)) {
                        this.videos.push(...item.result);
                    }
                });
            } catch (error) {
                console.error('ËØ∑Ê±ÇÂ§±Ë¥•:', error);
            } finally {
                this.isLoading = false; // Á°Æ‰øùÊó†ËÆ∫ËØ∑Ê±ÇÊàêÂäüÊàñÂ§±Ë¥•ÈÉΩÂÅúÊ≠¢Âä†ËΩΩÁä∂ÊÄÅ
            }
        }

    }
};
</script>
<style>
.home-page {
    background-color: #111519;
    color: #D9F2FF;
    padding: 20px;
    min-height: 100vh;
    margin-top: 100px;

}
</style>